.district h3 {
  padding: 5px;
  /* background-color: var(--bassanio-bg-clr); */
}

article>div.combine {
  padding: 5px;
}

.district .district-temperature {
  margin: 10px 5px 10px 0px;
}


.district .rainfall.card {
  margin: 10px 5px;
}

.district .combine h3,
.district-temperature h3 {
  font-weight: 600;
  text-decoration: underline;
}

.district .district-temperature,
.district .district-rainfall {
  min-width: 400px;
}

.district .card {
  min-width: 400px;
  min-height: auto;
}

.district .card .form-group {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
  padding: 2px 2px 2px 2px;
  margin: 1px 5px;
  background-color: var(--bassanio-bg-clr);
  font-size: 1.5rem;
  width: 100%;
}

.district .card .form-group label {
  float: left;
  width: 115px;
}

.district .card .form-group input {
  padding: 5px;
  background: transparent;
  width: 150px;
  border: 0px;
  text-align: center;
  background-color: var(--clr-gray-14);
  font-size: 2rem;
}

.district .combine {
  width: 100%;
  display: flex;
  flex-flow: wrap;
  justify-content: space-between;
}