.rhrread {
  padding: 5px;
  /* background: var(--bg-clr); */
}

.rhrread .uvindex-humidity {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px;
}

.rhrread .humidity,
.rhrread .uvindex {
  margin: 10px 5px;
}

.rhrread>div.uvindex-humidity>div.humidity>div.updateTime,
.rhrread>div.uvindex-humidity>div.uvindex>div.updateTime {
  padding: 0px 5px 10px 5px;
  background-color: var(--bassanio-bg-clr);
}

.rhrread>div.humidity-humidity>div.humidity>div.records,
.rhrread>div.uvindex-humidity>div.uvindex>div.records {
  padding: 5px;
}

.rhrread .combine .comfort .sub-rainfall h4,
.rhrread .combine .comfort .sub-temperature h4,
.rhrread>div>div.constrict>div.sub-rainfall>h4,
.rhrread>div>div.constrict>div.sub-temperature>h4,
.rhrread>div.uvindex-humidity>div.uvindex>h4,
.rhrread>div.uvindex-humidity>div.humidity>h4 {
  background: var(--bg-clr);
}

#updateTime {
  background: var(--bg-clr);
}

.rhrread>div>div>div.sub-rainfall,
.rhrread>div>div>div.sub-temperature {
  margin: 10px 5px;

}

.rhrread .ad {
  min-width: 390px;
  max-width: 390px;
  min-height: 350px;
  max-height: 350px;
}

.rhrread .records {
  padding: 5px;
  display: flex;
  flex-flow: wrap-reverse;
  align-items: center;
  justify-content: space-evenly;

}

.rhrread .form-group {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
  padding: 2px 2px 2px 2px;
  margin: 1px 5px;
  background-color: var(--bassanio-bg-clr);
}

.rhrread .form-group label {
  float: left;
  width: 115px;
}

.rhrread .form-group input {
  padding: 5px;
  background: transparent;
  width: 70px;
  border: 0px;
  text-align: center;
  background-color: var(--clr-gray-14);
  font-size: 1.25rem;
}

.rhrread .ad {
  margin: 5px 0 5px 0;
  background-color: transparent;
}

.rhrread .bottom-space {
  height: 0px;
}

.rhrread .combine {
  background: transparent;
}


@media (min-width: 940px) {
  .rhrread>div.combine>div.comfort {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  .rhrread>div.combine>div.constrict {
    display: none;
  }
}

@media (max-width: 939.99px) {
  .rhrread>div.combine>div.constrict {
    display: inline-block;
  }

  .rhrread>div.combine>div.comfort {
    display: none;
  }
}