.comparison-of-gregorian-and-lunar-calendar-dates {
    min-width: 100%;
}

.comparison-of-gregorian-and-lunar-calendar-dates .content {
    min-width: 100%;
}

.comparison-of-gregorian-and-lunar-calendar-dates .ad-s {
    min-width: 100%;
}

.comparison-of-gregorian-and-lunar-calendar-dates .ad-s .ad-1,
.comparison-of-gregorian-and-lunar-calendar-dates .ad-s .ad-2,
.comparison-of-gregorian-and-lunar-calendar-dates .ad-s .ad-3 {
    height: auto;
    display: flex;
    justify-content: center;
}

.comparison-of-gregorian-and-lunar-calendar-dates .user-interface {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    padding: 5px;
}

.content>div>div.ui-lef,
.content>div>div.ui-right {
    margin: 0 10px;
}

@media (max-width: 768px) {
    .comparison-of-gregorian-and-lunar-calendar-dates .user-interface {
        flex-direction: column;
    }
}

.comparison-of-gregorian-and-lunar-calendar-dates .ui-right .form-group {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 10px;
    padding: 2px 2px 2px 2px;
    margin: 1px 1px;
    background-color: var(--bassanio-bg-clr);
}

.content>div>div.ui-left>div>label {
    float: left;
    width: 85px;
}

.content>div>div.ui-right>div>label {
    float: left;
    width: 85px;
}

#use-date {
    padding: 5px;
    background: transparent;
    min-width: 195px;
}

.content>div>div.ui-right>div>input[type=text] {
    padding: 5px;
    background: transparent;
    min-width: 195px;
    border: 0px;
    background-color: var(--clr-gray-14);
    font-size: 1.25rem;
}

.comparison-of-gregorian-and-lunar-calendar-dates .ad {
    margin: 5px 0 5px 0;
    min-width: 100%;
    background-color: transparent;
}