.earthquake-info {
    display: flex;
    flex-wrap: wrap;
}

.earthquake-info .content {
    min-width: 390px;
    max-width: 800px;
}

.earthquake-info .ad {
    min-width: 390px;
    max-width: 390px;
    min-height: 350px;
    max-height: 350px;
}

.earthquake-info .info {
    display: column;
    align-items: center;
    gap: 10px;
    padding: 15px 5px 15px 5px;
}

.earthquake-info .type {
    padding: 5px;
}

.earthquake-info .qem {
    border-color: orange;
    border: 0 0 3px 0 solid;
    color: orange;

}

.earthquake-info .feltearthquake {
    border-color: red;
    border: 0 0 3px 0 solid;
    color: red;
    padding: 5px;
}

.earthquake-info .info .form-group {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 10px;
    padding: 2px 2px 2px 2px;
    margin: 1px 5px;
    background-color: var(--bassanio-bg-clr);
}

.earthquake-info .info .form-group label {
    float: left;
    width: 180px;
}

.earthquake-info .info .form-group input {
    padding: 5px;
    background: transparent;
    min-width: 200px;
    border: 0px;
    background-color: var(--clr-gray-14);
    font-size: 1.25rem;
}

.earthquake-info .ad {
    margin: 5px 0 5px 0;
    background-color: transparent;
}

.earthquake-info .ad-s {
    min-width: 100%;
}

.earthquake-infos .ad-s .ad-1,
.earthquake-infos .ad-s .ad-2,
.earthquake-infos .ad-s .ad-3 {
    height: auto;
    display: flex;
    justify-content: center;
}