.meteorological-and-weather-info body {
    background-color : rgb(182,240,247);
    
}

.meteorological-and-weather-info .title {
    font-size : 50px;
}

.meteorological-and-weather-info .sub-title {
    font-size : 2.0rem;
}

.meteorological-and-weather-info .LHL {
    border: 1px solid #333;
    border-radius: 8px;
    padding: 0.5rem;
    margin: 0.5rem 0;
    flex : 1;
    min-width: 300px; 
}

.meteorological-and-weather-info .LHL-LT {
    display : flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-item: center;
    justify-content: space-evenly;
}

.meteorological-and-weather-info .LHL .lightning {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 1rem;
    align-items: flex-start;
    font-size : 1rem;
}

.meteorological-and-weather-info .lightning .ND-info {
    display: flex;
    flex-direction: row;
    flex-warp: wrap;
}

.meteorological-and-weather-info .lightning .dt-from-data,
.meteorological-and-weather-info .lightning .dt-end-data,
.meteorological-and-weather-info .lightning .cat-data,
.meteorological-and-weather-info .lightning .dist-data,
.meteorological-and-weather-info .lightning .lightfreq-data {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    padding: 0.2rem 0;
    border-top: 1px solid #eee;
    min-height: 2rem;
    gap: 0.2rem;
}


.meteorological-and-weather-info .LTMV {
    border: 1px solid #333;
    border-radius: 8px;
    padding: 0.5rem;
    margin: 0.5rem 0;
    min-width: 300px;
}


    
.meteorological-and-weather-info .LTMV .visibility {
    display: flex;
    /* flex-direction: column; */
    font-size : 1rem;
    flex-wrap: wrap;
    gap: 0.5rem;
    align-items: flex-start;
    justify-content : center;
    border : none;
}


.meteorological-and-weather-info .visibility .dt-value,
.meteorological-and-weather-info .visibility .area,
.meteorological-and-weather-info .visibility .v-value {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    padding: 0.5rem 0;
    border-top: 1px solid #eee; 
}




.meteorological-and-weather-info .LH-ys, .LH-ye, 
.meteorological-and-weather-info .LH-ms, .LH-me, 
.meteorological-and-weather-info .LH-ds, .LH-de {
    display: inline-block;
    margin: 0 0.1rem;
}