.fnd {
  padding: 5px;
  background: var(--bg-clr);
}

.fnd .weatherForecast {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-gap: 1px;
  width: 100%;
}

.fnd .weatherForecast .date {
  display: flex;
  flex-direction: column;
  text-align: center;
  color: white;
  background: var(--clr-green-5);
}

#fnd>div.weatherForecast>div>img {
  background: var(--clr-green-5);
}

.fnd .weatherForecast .date:hover {
  color: var(--clr-green-3);
  border: 1px solid var(--clr-green-3);
  background: whitesmoke;
  font-weight: 700;
}

.fnd>.weatherForecast .forecastDate {
  min-width: 80px
}

.fnd p,
.update-time,
.fnd>.weatherForecast .forecastDate,
.fnd>.weatherForecast>div>img,
.fnd>.weatherForecast>div>div {
  padding: 5px;
}

.fnd .weatherForecast .date img {
  align-self: center;
}

.fnd>.weatherForecast>div>.desc>.forecastWind,
.fnd>.weatherForecast>div>.desc>.forecastWeather {
  text-align: left;
}

@media (max-width: 992px) {
  #fnd>div.weatherForecast {
    display: grid;
    grid-template-columns: 12fr;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-gap: 1px;
    width: 100%;
  }

  .fnd .weatherForecast .date {
    display: flex;
    flex-direction: row;
    text-align: center;
    align-items: center;
    color: white;
    background: var(--clr-green-5);
  }

  .fnd .weatherForecast .date:hover {
    color: var(--clr-green-3);
    border: 1px solid var(--clr-green-3);
    background: whitesmoke;
    font-weight: 700;
  }
}