.automatic-weather-station {
    display: flex;
    flex-wrap: wrap;
}

.automatic-weather-station .content {
    min-width: 390px;
    /* max-width: 800px; */
}

#obsTime {
    padding: 5px;
}

.automatic-weather-station .ad {
    min-width: 390px;
    max-width: 390px;
    min-height: 350px;
    max-height: 350px;
}

.automatic-weather-station .records {
    padding: 5px;
    display: flex;
    flex-flow: wrap-reverse;
    align-items: center;
    justify-content: space-evenly;
}

.automatic-weather-station .form-group {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 10px;
    padding: 2px 2px 2px 2px;
    margin: 1px 5px;
    background-color: var(--bassanio-bg-clr);
}

.automatic-weather-station .form-group label {
    float: left;
    width: 115px;
}

.automatic-weather-station .form-group input {
    padding: 5px;
    background: transparent;
    width: 70px;
    border: 0px;
    text-align: center;
    background-color: var(--clr-gray-14);
    font-size: 1.25rem;
}

.automatic-weather-station .ad {
    margin: 5px 0 5px 0;
    background-color: transparent;
}

.automatic-weather-station .bottom-space {
    height: 0px;
}

@media (max-width: 768px) {
    .automatic-weather-station .bottom-space {
        min-height: 100px;
    }
}