.home .flex-container,
.home .ad-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    padding: 20px;
    max-width: 1200px;
    margin: 0 auto;
}

.home .ad-container {
    padding-top: 20px;
    padding-left: 0px;
    padding-right: 0px;
    max-width: 900px;
}

.home .ad-home-1 {
    width: 100%;
    margin: 0 auto;
}

.home .flex-item {
    flex: 1 1 300px;
    min-width: 250px;
    max-width: 350px;
    background: rgba(255, 255, 255, 09);
    border-radius: 10px;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    color: var(--clr-green-7);
    font-weight: 900;
    transition: transform 0.3 ease;
    -webkit-transition: transform 0.3 ease;
    -moz-transition: transform 0.3 ease;
    -ms-transition: transform 0.3 ease;
    -o-transition: transform 0.3 ease;
}

.home .flex-item:hover {
    transform: translateY(-5px);
    -webkit-transform: translateY(-5px);
    -moz-transform: translateY(-5px);
    -ms-transform: translateY(-5px);
    -o-transform: translateY(-5px);
}

/*.weather-info*/
.home .records {
    padding: 5px;
}

/*.weather-info*/
.home .records .updateTime,
.home .records .desc {
    padding: 5px;
}