.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.app-logo {
  height: 65px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  /* border: 1px solid red; */
}

:root {
  --navbar-height: 3.5rem;
  --clr-green-1: rgb(12, 25, 10);
  --clr-green-2: rgb(20, 40, 18);
  --clr-green-3: rgb(28, 57, 26);
  --clr-green-4: rgb(33, 80, 40);
  --clr-green-5: rgb(37, 95, 56);
  --clr-green-6: rgb(31, 110, 70);
  --clr-green-7: rgb(27, 125, 83);
  --clr-green-8: rgb(34, 140, 100);
  --clr-green-9: rgb(38, 155, 118);
  --clr-green-10: rgb(42, 170, 135);
  --clr-gray-1: rgb(15, 15, 15);
  --clr-gray-2: rgb(28, 28, 28);
  --clr-gray-3: rgb(39, 39, 39);
  --clr-gray-4: rgb(57, 57, 57);
  --clr-gray-5: rgb(75, 75, 75);
  --clr-gray-6: rgb(95, 95, 95);
  --clr-gray-7: rgb(115, 115, 115);
  --clr-gray-8: rgb(135, 135, 135);
  --clr-gray-9: rgb(155, 155, 155);
  --clr-gray-10: rgb(175, 175, 175);
  --clr-gray-11: rgb(195, 195, 195);
  --clr-gray-12: rgb(215, 215, 215);
  --clr-gray-13: rgb(235, 235, 235);
  --clr-gray-14: rgb(245, 245, 245);
  --clr-gray-15: rgb(255, 255, 255);
  /* Pure white */
  --clr-gray-16: rgb(240, 240, 240);
  --clr-gray-17: rgb(220, 220, 220);
  --clr-gray-18: rgb(200, 200, 200);
  --clr-gray-19: rgb(180, 180, 180);
  --clr-gray-20: rgb(160, 160, 160);

  --nav-link-font-size: 1rem;
  --nav-link-font-color: white;
  --bassanio-bg-clr: hsl(185, 94%, 87%);
  --bg-clr: var(--bassanio-bg-clr);
}

html {
  font-size: 18px;
  line-height: normal;
  letter-spacing: 1px;
  word-spacing: 3px;
  font-family: Arial, Helvetica, sans-serif;
  scroll-behavior: 3px;
  background: black;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: white;
  color: var(--clr-green-2);
  background-color: var(--bg-clr);
  background-image: url("./img/wallpaperflarewallpaper.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.container-fluid {
  margin: 0 auto;
  justify-content: center;
  width: 95%;
}

.navbar {
  /* position: fixed; */
  top: 0;
  left: 0;
  width: 100%;
  min-height: var(--navbar-height);
  background: var(--clr-green-3);
  box-shadow: var(--clr-gray-3);
  z-index: 2;
  color: var(--nav-link-font-color);
}

.navbar {
  color: var(--nav-link-font-color);
  padding: 0 15px;
}

.nav-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.nav-content h1 {
  display: block;
  position: relative;
}

.nav-header {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  align-items: center;
  position: relative;
  width: 100%;
}

ul:not(.about > div.content > div.announcement > ul) {
  list-style: none;
}

a {
  text-decoration: none;
  cursor: pointer;
}

.nav-header .nav-links {
  background: var(--clr-green-3);
  font-size: var(--nav-link-font-size);
  margin: 0 15px;
  text-transform: capitalize;
  left: 10px;
}

.nav-links>li>ul.sub-nav-links {
  /* background: var(--clr-green-5); */
}

.nav-header .nav-links li {
  display: inline-block;
  min-height: 34px;
}

.nav-toggle {
  display: none;
}

.scroll-link li {
  padding: 5px 5px 5px 0px;
}

.nav-links li a {
  color: var(--nav-link-font-color);
  font-weight: 900;
  padding: 5px;
}

.nav-links li a:hover {
  background: var(--clr-green-8);
  color: var(--nav-link-font-color);
}

.nav-links .dropdown {
  position: relative;
}

.nav-links .dropdown a {
  display: block;
}

.nav-links .dropdown .sub-nav-links {

  position: absolute;
  background: transparent;
  list-style-type: none;
  min-width: 180px;
  z-index: 3000;
  left: 0px;
  display: none;
}

.nav-links .dropdown .show-dropdown {
  display: block;
}

.nav-links .dropdown .sub-nav-links li {
  width: 100%;
}

.nav-links .dropdown .sub-nav-links li a {
  color: var(--clr-green-3);
  font-weight: 900;
  padding: 5px;
  background-color: var(--clr-gray-15);
  display: block;
  width: 100%;
}

.nav-links .dropdown .sub-nav-links li a:hover {
  color: var(--nav-link-font-color);
  background-color: var(--clr-green-7);
}

@media (max-width: 768px) {
  .nav-toggle {
    display: block;
    padding: 15px;
    outline: none;
    border: none;
  }

  .nav-links {
    display: none;
  }

  .show-links {
    display: flex;
    flex-direction: column;
    top: 41px;
    left: -100px;
    position: absolute;
  }

  .nav-links>li>a {
    display: block;
  }

  .nav-links .dropdown .sub-nav-links {
    left: 190px;
  }

  .nav-links .dropdown .sub-nav-links {
    max-height: 400px;
    overflow: auto;
  }

}

label,
input {
  cursor: pointer;
}

label {
  font-weight: 1000;
}

/*. General */