.weather-info .content {
  min-width: 100%;
}

.weather-info .content .buttons {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: last baseline;
  padding: 5px;
}

.weather-info .content .buttons li {
  margin: 0px 5px;
  padding: 5px 0px;
}

.weather-info .content .buttons a {
  padding: 5px 5px 5px 5px;
  background: transparent;
  color: var(--clr-green-5);
  font-weight: 900;
}

.weather-info .content .buttons a:hover {
  background: var(--clr-green-4);
  color: white;
}

.weather-info .records {
  padding: 5px;
}

.weather-info .records .updateTime,
.weather-info .records .desc {
  padding: 5px;
}